import { Component, ChangeDetectionStrategy, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { AccountNumberFormat } from '@backbase/billpay-journeys-common';
import { Account } from '@backbase/data-ang/billpay';

@Component({
  selector: 'bb-billpay-payment-compact-product-selector',
  templateUrl: './billpay-payment-compact-product-selector.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentCompactProductSelectorComponent {
  @Input() paymentForm?: FormGroup;
  @Input() accounts?: Account[];
  @Input() productNumberFormat?: AccountNumberFormat;
  @Output() selectItem = new EventEmitter<Account>();

  onSelectItem(account: Account) {
    this.selectItem.emit(account);
  }
}
