import { Component, Input, OnInit } from '@angular/core';
import { Currency } from '@backbase/data-ang/billpay';
import { FormControl } from '@angular/forms';
import { BillpayUtilityService as Utility, PaymentAmount } from '@backbase/billpay-journeys-common';

export interface CurrencyUI {
  amount: string;
  currency: string;
}

export interface LatestBill {
  id?: string;
  paymentDate?: string;
  amount?: Currency;
  minAmountDue?: Currency;
  outstandingBalance?: Currency;
  status?: string;
  url?: string;
  statementAvailable?: boolean;
}

@Component({
  selector: 'bb-billpay-ebill-payment',
  templateUrl: './billpay-ebill-payment-ui.component.html',
})
export class BillpayEbillPaymentUIComponent implements OnInit {
  /**
   * Details of the latest eBill to be paid.
   */
  @Input() latestBill?: LatestBill;
  /**
   * Form control for the eBill payment form.
   */
  @Input() control?: FormControl;
  /**
   * Mark the form as submitted, controls validation elements. Defaults to false.
   */
  @Input() isSubmitted = false;

  /**
   * get the payment default currency preference. default to 'USD'.
   */
  @Input() eBillDefaultCurrency = 'USD';

  /**
   * unique key used for ids
   */
  readonly id = Utility.generateId();
  touched?: boolean;

  public customValue!: PaymentAmount;

  ngOnInit() {
    this.customValue = {
      amount: '',
      currency: this.eBillDefaultCurrency,
      type: undefined,
    };
  }

  setAmount(type: string) {
    this.customValue.type = type;
    if (type === 'Custom') {
      this.setCustomAmount(this.customValue.amount);
    } else {
      const currency = this.getAmount(type);
      if (currency) {
        this.setValue({
          amount: currency.amount,
          currency: currency.currencyCode,
          type: type,
        });
      }
    }
  }

  setCustomAmount(ev: string) {
    this.customValue.amount = ev;
    this.setValue({ ...this.customValue, type: 'Custom' });
  }

  get showBalance(): boolean {
    return !!(
      this.latestBill && this.isDifferentAndNonZero(this.latestBill.outstandingBalance, this.latestBill.amount)
    );
  }

  get showMinumum(): boolean {
    return !!(this.latestBill && this.isDifferentAndNonZero(this.latestBill.minAmountDue, this.latestBill.amount));
  }

  private isDifferentAndNonZero(otherEbillAmount: Currency | undefined, ebillAmount: Currency | undefined): boolean {
    return !!(
      otherEbillAmount &&
      ebillAmount &&
      otherEbillAmount.amount !== ebillAmount.amount &&
      0 !== +otherEbillAmount.amount
    );
  }

  private setValue(newValue: any) {
    if (this.control) {
      this.control.setValue(newValue);
    }
  }

  private getAmount(value: string) {
    switch (value) {
      case 'Minimum':
        return this.latestBill && this.latestBill.minAmountDue;
      case 'Balance':
        return this.latestBill && this.latestBill.outstandingBalance;
      case 'Due':
      default:
        return this.latestBill && this.latestBill.amount;
    }
  }
}
