import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Frequency, BillpayUtilityService as Utility } from '@backbase/billpay-journeys-common';
import { FrequencyKeys } from '@backbase/billpay-journeys-common';

@Component({
  selector: 'bb-billpay-payment-start-date',
  templateUrl: './billpay-payment-start-date.component.html',
})
export class BillpayPaymentStartDateComponent {
  @Input() scheduleForm?: FormGroup;
  @Input() frequencies?: Array<Frequency>;
  @Input() minDate: string | undefined;
  frequencyValues = FrequencyKeys;
  readonly id = Utility.generateId();

  get formValue() {
    return this.scheduleForm?.getRawValue();
  }
}
