import { Component, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { BillpayPaymentReviewContainerComponent } from '../components/billpay-payment-single-bill/billpay-payment-review-container/billpay-payment-review-container.component';
import { PayBillsJourneyConfigService } from '../services/pay-bills-journey-config.service';
import { PayBillsNavigationService } from '../services/pay-bills-navigation.service';

@Component({
  template: `
    <div class="bb-stack">
      <div class="bb-block bb-block--sm bb-stack__item bb-stack__item--fill">
        <button bbButton color="link" class="bb-block bb-block--md pl-0" (click)="goBack()">
          <i aria-hidden="true" class="bb-icon bb-icon-arrow-back"></i>
          <span i18n="button label|Back button@@pay-bills-journey.payee-list.back">Back to Payee List</span>
        </button>
        <bb-header-ui
          headingType="h1"
          heading="Review your payment"
          i18n-heading="@@pay-bills-journey.title.make-a-payment"
          data-role="pay-a-bill-journey-review-title"
        >
        </bb-header-ui>
      </div>
    </div>
    <bb-billpay-payment-review-container
      [accountNumberMasked]="config.accountNumberMasked"
      [dismissTimeoutValue]="config.notificationDismissTime"
      [frequencies]="config.frequencies"
      #paymentReviewContainer
    ></bb-billpay-payment-review-container>
  `,
})
export class BillpayPaymentReviewViewComponent {
  @ViewChild('paymentReviewContainer') child!: BillpayPaymentReviewContainerComponent;
  constructor(
    readonly config: PayBillsJourneyConfigService,
    private readonly routerService: PayBillsNavigationService,
  ) {}
  openModal(): Observable<boolean> {
    return this.child.openModal();
  }
  goBack() {
    this.routerService.navigateToPayeeList();
  }
}
