import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostListener,
  Input,
  OnDestroy,
  OnInit,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { filter, switchMap, take, takeUntil, tap } from 'rxjs/operators';
import {
  BillpayDiscardChangesFormComponent,
  BillpayDiscardChangesModalService,
  BillpayPaymentParserService,
  PaymentFormState,
  PaymentReview,
  Frequency,
  BillpayPaymentDataService,
} from '@backbase/billpay-journeys-common';
import { PayBillsStateService } from '../../../services/pay-bills-state.service';
import { PayBillsNavigationService } from '../../../services/pay-bills-navigation.service';
import { ActivatedRoute } from '@angular/router';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-billpay-payment-review-container',
  templateUrl: './billpay-payment-review-container.component.html',
  providers: [BillpayDiscardChangesModalService],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentReviewContainerComponent implements OnInit, OnDestroy, BillpayDiscardChangesFormComponent {
  payment?: PaymentFormState;
  paymentReview?: PaymentReview;
  private readonly paymentConfirm$ = new BehaviorSubject<PaymentFormState | undefined>(undefined);
  private readonly componentDestroyed$ = new Subject<void>();
  @Input() frequencies?: Frequency[];
  @Input() dismissTimeoutValue: number | undefined;
  @Input() accountNumberMasked?: boolean;
  paymentError = false;
  hasDateError = false;
  frequencyLabel?: string;
  private editMode = false;

  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';
  @ViewChild('failAlert') failAlert!: ElementRef;

  /** @internal */
  constructor(
    private readonly navigationService: PayBillsNavigationService,
    private readonly dataService: BillpayPaymentDataService,
    private readonly stateService: PayBillsStateService,
    private readonly notificationService: NotificationService,
    private readonly discardModalService: BillpayDiscardChangesModalService,
    private readonly paymentParserService: BillpayPaymentParserService,
    private readonly route: ActivatedRoute,
    private cdRef: ChangeDetectorRef,
  ) {}

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  private get dismissTimeout(): number {
    if (this.dismissTimeoutValue === undefined) {
      throw new Error('Dismiss timeout value not found');
    }
    return this.dismissTimeoutValue;
  }

  ngOnInit() {
    this.stateService.paymentState.pipe(take(1)).subscribe(payment => {
      this.payment = payment?.[0];
      this.paymentReview = this.payment && this.paymentParserService.paymentFormStateToReview(this.payment);
      const frequency = this.frequencies?.find(freq => this.payment?.schedule?.frequency === freq.value);
      this.frequencyLabel = frequency?.name || '';
      if (!this.payment) {
        this.navigationService.navigateToForm({ relativeTo: this.route.parent });
      }
    });
    this.paymentConfirm$
      .pipe(takeUntil(this.componentDestroyed$), filter(Boolean))
      .subscribe(payment => this.onPaymentConfirm(payment as PaymentFormState));
  }

  ngOnDestroy(): void {
    this.componentDestroyed$.next();
    this.componentDestroyed$.complete();
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if (!this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  onEdit() {
    this.editMode = true;
    this.navigationService.navigateToForm({ relativeTo: this.route.parent });
  }

  navigateToPayments() {
    this.navigationService.navigateToPayments();
  }

  cancel() {
    this.navigationService.navigateToPayeeList();
  }

  confirmPayment(payment?: PaymentFormState) {
    if (!payment) return;
    this.paymentConfirm$.next(payment);
  }

  openModal(): Observable<boolean> {
    return this.paymentConfirm$.pipe(
      switchMap(payment => (payment || this.editMode ? of(true) : this.discardModalService.openModal())),
      tap(isResolved => isResolved && !this.editMode && this.stateService.reset()),
    );
  }

  onModalResolve(isConfirmed: boolean) {
    this.discardModalService.onModalResolve(isConfirmed);
  }

  private onPaymentConfirm(payment: PaymentFormState) {
    this.paymentError = false;
    this.hasDateError = false;
    this.dataService
      .createPayment(payment)
      .pipe(takeUntil(this.componentDestroyed$))
      .subscribe(
        () => {
          this.navigationService.navigateToPayeeList();
          this.notificationService.showNotification({
            header: this.notificationHeaderTemplate,
            message: this.notificationMessageTemplate,
            modifier: 'success',
            ttl: this.dismissTimeout,
          });
        },
        response => {
          this.paymentError = true;
          this.hasDateError = response.error?.errors?.some((a: any) => a.key === 'billpay.api.invalidPaymentDate');
          this.cdRef.markForCheck();
          this.failAlert.nativeElement.scrollIntoView(false);
        },
      );
  }
}
