import { Component, ChangeDetectionStrategy, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PaymentFormState, BillpayUtilityService as Utility } from '@backbase/billpay-journeys-common';
import { AssetsResolver, AssetsService } from '@backbase/foundation-ang/core';
import { Observable } from 'rxjs';
import { PayBillsNavigationService } from '../../../services/pay-bills-navigation.service';
import { PaymentResult } from '../../../model/payment.model';

declare const $localize: any;

@Component({
  selector: 'bb-billpay-payment-multiple-bills-success',
  templateUrl: 'billpay-payment-multiple-bills-success.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentMultipleBillsSuccessComponent {
  readonly id = Utility.generateId();
  successMsg?: string;
  mixedMsg?: string;
  allSucceeded = true;
  payments?: PaymentResult[];

  @Input() set results(value: PaymentResult[]) {
    const succeded = value.filter(payment => payment.succeeded);
    this.allSucceeded = succeded.length === value.length;
    if (this.allSucceeded) {
      const getPayee = (payment: PaymentFormState) => payment.payeeTo.nickName || payment.payeeTo.name;
      const payees = value
        .map((payment, i) => {
          if (i === 0) return getPayee(payment);
          return i === value.length - 1 ? ' and ' + getPayee(payment) : ', ' + getPayee(payment);
        })
        .join('');
      this.successMsg = $localize`Youve just scheduled ${value.length} payment${
        value.length > 1 ? 's' : ''
      } to ${payees}.`;
    } else {
      this.payments = value;
      this.mixedMsg = $localize`You scheduled sucessfully ${succeded.length} out of ${value.length} bills.`;
    }
  }

  private readonly resolveAsset: AssetsResolver = this.assetsService.createAssetsResolver({
    widgetName: 'bb-pay-bills-journey',
    getURIFromAssetID: (assetId: string) => `${assetId}`,
  });

  successImg: Observable<string> = this.resolveAsset('succeed.svg');
  successImgNoMotion: Observable<string> = this.resolveAsset('no-motion.svg');

  constructor(
    private readonly assetsService: AssetsService,
    private readonly navigationService: PayBillsNavigationService,
    private readonly route: ActivatedRoute,
  ) {}

  payMore() {
    this.navigationService.navigateToForm({ relativeTo: this.route.parent });
  }
  goToMyAccounts() {
    this.navigationService?.navigateToMainAppPage();
  }
}
