import { Component, ChangeDetectionStrategy, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { BillpayUtilityService as Utility } from '@backbase/billpay-journeys-common';
import { Account, PayeeSummary } from '@backbase/data-ang/billpay';
@Component({
  selector: 'bb-billpay-payment-multiple-bills-list',
  templateUrl: './billpay-payment-multiple-bills-list.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentMultipleBillsListComponent {
  _payeeSummaries: PayeeSummary[] = [];
  @Input() set payeeSummaries(value: PayeeSummary[]) {
    this._payeeSummaries = this.sortByDueDate(value);
  }
  get payeeSummaries() {
    return this._payeeSummaries;
  }
  @Input() paymentsFormGroup?: FormGroup;
  @Input() accounts?: Account[];
  @Input() isForEbills = false;
  readonly id = Utility.generateId();

  /**
   * Sorting payee summaries by due date. Initial view display ascending list(earliest date first)
   */
  private sortByDueDate(payeeSummaries = this.payeeSummaries) {
    const payeeWithoutDate = payeeSummaries.filter(payee => !payee.ebill?.latestBill?.paymentDate);
    const payeeWithDate = payeeSummaries
      .filter(payee => !!payee.ebill?.latestBill?.paymentDate)
      .sort((payee1: PayeeSummary, payee2: PayeeSummary) => {
        const date1 = new Date(payee1.ebill?.latestBill?.paymentDate || '').getTime();
        const date2 = new Date(payee2.ebill?.latestBill?.paymentDate || '').getTime();
        return this.ascendingList ? date1 - date2 : date2 - date1;
      });
    return [...payeeWithDate, ...payeeWithoutDate];
  }
  /**
   * Indicates the way payees are sorted in the list
   */
  ascendingList = true;

  /**
   * Set sorted payees
   */
  onSortClick() {
    this.ascendingList = !this.ascendingList;
    this.payeeSummaries = this.sortByDueDate();
  }

  /**
   * Set sorted payees
   */
  trackByFunction(_index: number, payee: PayeeSummary) {
    return payee.ebill?.latestBill?.paymentDate;
  }
}
