import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable, combineLatest, of } from 'rxjs';

import { map, switchMap, catchError } from 'rxjs/operators';
import {
  BillpayDiscardChangesModalService,
  BillpayPaymentDataService,
  BillpayPaymentParserService,
  Frequency,
  PaymentFormState,
} from '@backbase/billpay-journeys-common';

import { PayBillsStateService } from '../../../services/pay-bills-state.service';
import { PayBillsNavigationService } from '../../../services/pay-bills-navigation.service';
import { BasePaymentEditFormContainerDirective } from '../../../directives/base-payment-edit-form-container.model';

@Component({
  selector: 'bb-billpay-payment-recurring-edit-form-container',
  templateUrl: './billpay-payment-recurring-edit-form-container.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class BillpayPaymentRecurringEditFormContainerComponent
  extends BasePaymentEditFormContainerDirective
  implements OnInit {
  paymentRecurringState$?: Observable<PaymentFormState | PaymentFormState[]>;
  hasLoadingError = false;
  private activatedRoute?: ActivatedRoute;

  @Input() frequencies?: Frequency[];

  /** @internal */
  constructor(
    dataService: BillpayPaymentDataService,
    stateService: PayBillsStateService,
    route: ActivatedRoute,
    discardModalService: BillpayDiscardChangesModalService,
    readonly routerService: PayBillsNavigationService,
    private readonly parserService: BillpayPaymentParserService,
  ) {
    super(dataService, stateService, route, discardModalService, routerService);
    this.activatedRoute = route;
  }

  ngOnInit() {
    this.paymentRecurringState$ = combineLatest([
      this.id$,
      this.stateService.paymentState.pipe(map((state?: PaymentFormState[]) => state?.[0])),
      this.payeesAndAccounts$,
    ]).pipe(
      switchMap(([id, state, data]) => {
        return (
          (state && of(state)) ||
          this.dataService.getRecurringPayment(id).pipe(
            map(
              payment =>
                payment &&
                data?.payeeSummaries &&
                this.parserService.paymentRecurringToFormState(payment, data.payeeSummaries),
            ),
            catchError(() => {
              this.hasLoadingError = true;
              return of({} as PaymentFormState);
            }),
          )
        );
      }),
    );
  }

  onPaymentSubmitted(data: PaymentFormState, id: string) {
    this.stateService.isFormProcessing = true;
    this.stateService.updatePaymentState([data]);
    this.routerService.navigateToEditRecurringReview(id, { relativeTo: this.activatedRoute?.parent });
  }
}
