import { Component, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { BillpayPaymentRecurringEditFormContainerComponent } from '../components/billpay-payment-single-bill/billpay-payment-recurring-edit-form-container/billpay-payment-recurring-edit-form-container.component';
import { PayBillsJourneyConfigService } from '../services/pay-bills-journey-config.service';
import { PayBillsNavigationService } from '../services/pay-bills-navigation.service';

@Component({
  template: `
    <div class="bb-block">
      <button bbButton color="link" class="bb-block bb-block--md pl-0" (click)="goBack()">
        <i aria-hidden="true" class="bb-icon bb-icon-arrow-back"></i>
        <span i18n="button label|Back button@@pay-bills-journey.payments-list.back">Back to payments list</span>
      </button>
    </div>
    <bb-billpay-payment-recurring-edit-form-container
      [accountNumberMasked]="config.accountNumberMasked"
      [paymentDetailsTitle]="config.paymentDetailsTitle"
      [frequencies]="config.frequencies"
      #paymentRecurringEditForm
    ></bb-billpay-payment-recurring-edit-form-container>
  `,
})
export class BillpayPaymentRecurringEditFormViewComponent {
  @ViewChild('paymentRecurringEditForm') child!: BillpayPaymentRecurringEditFormContainerComponent;
  constructor(
    readonly config: PayBillsJourneyConfigService,
    private readonly routerService: PayBillsNavigationService,
  ) {}
  openModal(): Observable<boolean> {
    return this.child.openModal();
  }
  goBack() {
    this.routerService.navigateToPayments();
  }
}
