import { Injectable, Optional } from '@angular/core';
import { ActivatedRoute, NavigationExtras, ParamMap, Router } from '@angular/router';
import { ONE_OFF_PAYMENT, RECURRING_PAYMENT } from '@backbase/billpay-journeys-common';
import { combineLatest, Observable } from 'rxjs';
import { map, pluck } from 'rxjs/operators';
import { CommunicationService } from '../communication.service';
import { PaymentRoutePageTypeParam, PaymentRouteParams, PaymentRoutePaymentTypeParam } from '../model/route.model';

@Injectable()
export class PayBillsNavigationService {
  private readonly defaultRelative: NavigationExtras;

  readonly routeParams: Observable<PaymentRouteParams> = combineLatest([
    this.getRouteParam(this.route, 'payee'),
    this.getRouteParam(this.route, 'id'),
  ]).pipe(
    map(([payee, id]) => {
      const pageType: PaymentRoutePageTypeParam = !!id ? 'edit' : 'new';
      const paymentType: PaymentRoutePaymentTypeParam = this.extractPaymentTypeFromId(id, payee);
      const paymentId = (id && id.substring(2)) || '';
      const payeeId = (payee && payee.substring(2)) || '';

      return {
        pageType,
        paymentType,
        ...(!!paymentId && { paymentId }),
        ...(!!payeeId && { payeeId }),
      };
    }),
  );

  constructor(
    private readonly router: Router,
    private readonly route: ActivatedRoute,
    @Optional() private readonly communicationService: CommunicationService,
  ) {
    this.defaultRelative = { relativeTo: this.route };
  }

  navigateToEditRecurringForm(id: string, extras?: NavigationExtras) {
    this.router.navigate(['recurring', 'edit', id], extras || this.defaultRelative);
  }

  navigateToEditRecurringReview(id: string, extras?: NavigationExtras) {
    this.router.navigate(['recurring', 'edit', id, 'review'], extras || this.defaultRelative);
  }

  navigateToEditOneOffForm(id: string, extras?: NavigationExtras) {
    this.router.navigate(['one-off', 'edit', id], extras || this.defaultRelative);
  }

  navigateToEditOneOffReview(id: string, extras?: NavigationExtras) {
    this.router.navigate(['one-off', 'edit', id, 'review'], extras || this.defaultRelative);
  }

  navigateToForm(extras?: NavigationExtras) {
    this.router.navigate(['form'], extras || this.defaultRelative);
  }

  navigateToReview(extras?: NavigationExtras) {
    this.router.navigate(['review'], extras || this.defaultRelative);
  }

  navigateToPayeeList() {
    this.communicationService?.navigateToPayeesList();
  }
  navigateToPayments() {
    this.communicationService?.navigateToPaymentsList();
  }
  navigateToAddPayee() {
    this.communicationService?.navigateToAddPayee();
  }
  navigateToMainAppPage() {
    this.communicationService?.navigateToMainAppPage();
  }
  navigateToEditManualPayee(id: string) {
    this.communicationService?.navigateToEditManualPayee(id);
  }
  navigateToEditElectronicPayee(id: string) {
    this.communicationService?.navigateToEditElectronicPayee(id);
  }
  navigateToPayeeSummary(id: string) {
    this.communicationService?.navigateToPayeeSummary(id);
  }
  navigateToPayverisEbillEnrol(id: string) {
    this.communicationService?.navigateToPayverisEbillEnrol(id);
  }

  private extractPaymentTypeFromId(paymentId: string | null, payeeId: string | null): PaymentRoutePaymentTypeParam {
    if (!paymentId && !payeeId) {
      return ONE_OFF_PAYMENT;
    }
    const idToUse: string = (paymentId ? paymentId : payeeId) as string;
    return idToUse.substring(0, 1) === 'r' ? RECURRING_PAYMENT : ONE_OFF_PAYMENT;
  }

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));

    if (!route.parent) {
      return paramValue;
    }

    // Ensure that level that has actual value keeps it from being cleared.
    return combineLatest([paramValue, this.getRouteParam(route.parent, param)]).pipe(
      map(params => params[0] || params[1]),
    );
  }
}
