import { ChangeDetectionStrategy, ChangeDetectorRef, Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {
  BillpayDiscardChangesModalService,
  BillpayPaymentDataService,
  BillpayPaymentParserService,
  PaymentFormState,
} from '@backbase/billpay-journeys-common';

import { BasePaymentEditReviewContainerDirective } from '../../../directives/base-payment-edit-review-container.model';
import { PayBillsNavigationService } from '../../../services/pay-bills-navigation.service';
import { PayBillsStateService } from '../../../services/pay-bills-state.service';
import { takeUntil } from 'rxjs/operators';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-billpay-payment-one-off-edit-review-container',
  templateUrl: './billpay-payment-one-off-edit-review-container.component.html',
  providers: [BillpayDiscardChangesModalService],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentOneOffEditReviewContainerComponent extends BasePaymentEditReviewContainerDirective {
  private readonly activatedRoute?: ActivatedRoute;

  /** @internal */
  constructor(
    route: ActivatedRoute,
    stateService: PayBillsStateService,
    discardModalService: BillpayDiscardChangesModalService,
    paymentParserService: BillpayPaymentParserService,
    readonly routerService: PayBillsNavigationService,
    private readonly dataService: BillpayPaymentDataService,
    private readonly notificationService: NotificationService,
    private cdRef: ChangeDetectorRef,
  ) {
    super(route, stateService, discardModalService, paymentParserService, routerService);
    this.activatedRoute = route;
  }

  navigateToForm(id: string) {
    this.routerService.navigateToEditOneOffForm(id, { relativeTo: this.activatedRoute?.parent });
  }

  protected onPaymentConfirm(payment: PaymentFormState, id: string) {
    this.paymentError = false;
    this.hasDateError = false;

    this.dataService
      .updatePaymentOneOff(payment, id)
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        () => {
          this.routerService.navigateToPayments();
          this.notificationService.showNotification({
            header: this.notificationHeaderTemplate,
            message: this.notificationMessageTemplate,
            modifier: 'success',
            ttl: this.dismissTimeout,
          });
        },
        error => {
          this.paymentError = true;
          this.hasDateError = error.error?.errors?.some((a: any) => a.key === 'billpay.api.invalidPaymentDate');
          this.cdRef.detectChanges();
          this.failAlert.nativeElement.scrollIntoView(false);
        },
      );
  }
}
