import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import { distinctUntilChanged, takeUntil } from 'rxjs/operators';
import {
  BillpayPayeeDataService,
  BillpayPayeeSummaryItemBaseComponent,
  BillpayPaymentScheduleService,
  BillpayUtilityService as Utility,
  PaymentServicesTypes,
  Frequency,
} from '@backbase/billpay-journeys-common';

import { Account, PayeeSummary } from '@backbase/data-ang/billpay';
import { NgbDropdownConfig } from '@ng-bootstrap/ng-bootstrap';
import { PayBillsJourneyConfigService } from '../../../services/pay-bills-journey-config.service';
import { PayBillsNavigationService } from '../../../services/pay-bills-navigation.service';
import { MediaQueryService } from '@backbase/ui-ang/media-query-lib';

@Component({
  selector: 'bb-billpay-multiple-bills-list-item',
  templateUrl: './billpay-payment-multiple-bills-list-item.component.html',
})
export class BillpayPaymentMultipleBillsListItemComponent
  extends BillpayPayeeSummaryItemBaseComponent
  implements OnInit, OnDestroy {
  @Input() payeeSummary: PayeeSummary | undefined;
  @Input() paymentForm: FormGroup | undefined;
  @Input() accounts?: Account[];
  @Input() inheritedId?: string;
  minDate!: string;
  isCollapsed = true;
  frequencies?: Frequency[];
  id = Utility.generateId();
  readonly productNumberFormat = this.configService.productNumberFormat;

  get latestBill() {
    return this.payeeSummary?.ebill?.latestBill;
  }

  ngOnInit() {
    this.minDate = this.scheduleService.estimatedPaymentServiceDate(
      this.payeeSummary?.paymentServices,
      PaymentServicesTypes.Regular,
    );
    this.syncPeerFields(this.paymentForm?.get('schedule.sendDate'));
  }

  constructor(
    payeeDataService: BillpayPayeeDataService,
    dropdownButtonConfig: NgbDropdownConfig,
    private readonly mediaQueryService: MediaQueryService,
    private readonly scheduleService: BillpayPaymentScheduleService,
    private readonly configService: PayBillsJourneyConfigService,
    private readonly navigationService: PayBillsNavigationService,
  ) {
    super(payeeDataService, dropdownButtonConfig);
    this.frequencies = this.configService.frequencies;
  }

  // Sync peer datepicker fields that are duplicated for the sake of responsive designs
  private syncPeerFields(formControl?: AbstractControl | null) {
    this.mediaQueryService
      .isMediaBreakpointMatches('max-width', 'lg')
      .pipe(takeUntil(this.destroy$), distinctUntilChanged())
      .subscribe(() => {
        formControl?.patchValue(formControl.value, { emitEvent: false, onlySelf: true });
      });
  }

  selectItem(account: Account) {
    this.paymentForm?.patchValue({ accountFrom: account });
  }

  handleEditPayee(): void {
    if (this.payeeSummary && !this.payeeSummary.electronic) {
      this.navigationService.navigateToEditManualPayee(this.payeeSummary.id);
    } else if (this.payeeSummary?.electronic) {
      this.navigationService.navigateToEditElectronicPayee(this.payeeSummary.id);
    }
  }

  handleViewSummary(): void {
    if (this.payeeSummary) {
      this.navigationService.navigateToPayeeSummary(this.payeeSummary.id);
    }
  }

  handleGetEbills() {
    if (this.payeeSummary) {
      this.navigationService.navigateToPayverisEbillEnrol(this.payeeSummary.id);
    }
  }
}
