import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';
import { AccountNumberFormat, accountNumberFullFormat } from '@backbase/billpay-journeys-common';
import '@angular/localize/init';
import { Frequency, FrequencyKeys } from '@backbase/billpay-journeys-common';

export const PayBillsJourneyConfigurationToken = new InjectionToken<PayBillsJourneyConfiguration>(
  'PayBillsJourneyConfigurationToken injection token',
);

export interface PayBillsJourneyConfiguration {
  notificationDismissTime: number;
  accountNumberMasked: boolean;
  paymentDetailsTitle: string;
  paymentDefaultCurrency: string;
  multipleBillsMode: boolean;
  productNumberFormat?: AccountNumberFormat;
  frequencies: Frequency[];
}

const configDefaults: PayBillsJourneyConfiguration = {
  notificationDismissTime: 5000,
  accountNumberMasked: false,
  paymentDetailsTitle: $localize`:@@pay-bills-journey-form-payment-details-title:Payment details`,
  paymentDefaultCurrency: 'USD',
  multipleBillsMode: false,
  productNumberFormat: accountNumberFullFormat,
  frequencies: [
    { name: $localize`:@@billpay-common.frequency-label.once:Once`, value: FrequencyKeys.Once },
    { name: $localize`:@@billpay-common.frequency-label.weekly:Weekly`, value: FrequencyKeys.Weekly },
    {
      name: $localize`:@@billpay-common.frequency-label.every-two-week:Every two weeks`,
      value: FrequencyKeys.Biweekly,
    },
    { name: $localize`:@@billpay-common.frequency-label.monthly:Monthly`, value: FrequencyKeys.Monthly },
  ],
};

@Injectable()
export class PayBillsJourneyConfigService {
  private _config: PayBillsJourneyConfiguration;

  constructor(@Optional() @Inject(PayBillsJourneyConfigurationToken) config: PayBillsJourneyConfiguration) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
  }

  get defaults(): PayBillsJourneyConfiguration {
    return configDefaults;
  }
  get notificationDismissTime(): number {
    return this._config.notificationDismissTime;
  }
  get accountNumberMasked(): boolean {
    return this._config.accountNumberMasked;
  }
  get paymentDetailsTitle(): string {
    return this._config.paymentDetailsTitle;
  }
  get paymentDefaultCurrency(): string {
    return this._config.paymentDefaultCurrency;
  }
  get multipleBillsMode(): boolean {
    return this._config.multipleBillsMode;
  }
  get productNumberFormat(): AccountNumberFormat | undefined {
    return this._config.productNumberFormat;
  }
  get frequencies(): Frequency[] {
    return this._config.frequencies;
  }
}
