import {
  Component,
  ChangeDetectionStrategy,
  OnInit,
  ViewChild,
  TemplateRef,
  OnDestroy,
  HostListener,
  ChangeDetectorRef,
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import {
  BillpayDiscardChangesModalService,
  BillpayPaymentDataService,
  BillpayPaymentScheduleService,
  BillpayUtilityService as Utility,
  EndTypes,
  LoadingState,
  PaymentServicesTypes,
} from '@backbase/billpay-journeys-common';
import { HttpResponse } from '@backbase/foundation-ang/web-sdk';
import { ActivatedRoute } from '@angular/router';
import { map, shareReplay, skip, switchMap, take, takeUntil, tap } from 'rxjs/operators';
import { Account, Address, PayeeSummary, PaymentService, PaymentServiceSummary } from '@backbase/data-ang/billpay';
import { BillpayPayeeDataService } from '@backbase/billpay-journeys-common';
import { FrequencyKeys, PayeeSummariesByEbill, PaymentFormState } from '@backbase/billpay-journeys-common';

import { PayBillsStateService } from '../../services/pay-bills-state.service';
import { Observable, of, Subject } from 'rxjs';
import { PayBillsNavigationService } from '../../services/pay-bills-navigation.service';
import { PayBillsJourneyConfigService } from '../../services/pay-bills-journey-config.service';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-billpay-payment-multiple-bills',
  templateUrl: './billpay-payment-multiple-bills.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentMultipleBillsComponent implements OnInit, OnDestroy {
  payeeSummariesByEbillObservable$?: Observable<PayeeSummariesByEbill | undefined>;
  private payeeSummaries?: PayeeSummary[];
  private accounts?: Account[];
  paymentsFormGroup = this.fb.group({});
  totalAmount$: Observable<number> = this.paymentsFormGroup.valueChanges.pipe(
    map((forms: FormGroup) =>
      Object.values(forms).reduce((acc: number, form: PaymentFormState) => acc + +form.amount.amount, 0),
    ),
  );
  accounts$?: Observable<Account[]>;

  readonly selectedDeleteEbills$: Observable<PayeeSummary | undefined> = this.payeeDataService.selectedDeleteEbills;
  readonly selectedDeletePayee$: Observable<PayeeSummary | undefined> = this.payeeDataService.selectedDeletePayee;

  get isErrorState() {
    const payeeState = this.payeeDataService.payeesLoadingState;
    const accountState = this.paymentDataService.accountsLoadingState;
    return payeeState === 'error' || accountState === LoadingState.Error;
  }
  private readonly destroy$ = new Subject<void>();

  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';

  isDeleteModalOpened = false;
  isDeleteEbillsModalOpen = false;
  isNavigateModalOpened = false;
  notificationType = '';
  readonly id = Utility.generateId();

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if ((this.paymentsFormGroup.touched || this.paymentStateService.isFormProcessing) && !this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  constructor(
    private readonly payeeDataService: BillpayPayeeDataService,
    private readonly fb: FormBuilder,
    private readonly scheduleService: BillpayPaymentScheduleService,
    private readonly paymentStateService: PayBillsStateService,
    private readonly navigationService: PayBillsNavigationService,
    private readonly route: ActivatedRoute,
    private readonly paymentDataService: BillpayPaymentDataService,
    private readonly configService: PayBillsJourneyConfigService,
    private readonly notificationService: NotificationService,
    private readonly discardModalService: BillpayDiscardChangesModalService,
    private cdRef: ChangeDetectorRef,
  ) {}

  ngOnInit() {
    this.payeeSummariesByEbillObservable$ = this.payeeDataService.getPayees().pipe(
      tap(payeeSummariesByEbill => {
        const { ebillsEnabled = [], ebillsNotEnabled = [] } = payeeSummariesByEbill || ({} as PayeeSummariesByEbill);
        this.payeeSummaries = [...ebillsEnabled, ...ebillsNotEnabled];
        this.createForm(this.payeeSummaries);
        if (this.paymentStateService.isFormProcessing) {
          this.patchPaymentDataToForm();
        }
      }),
      shareReplay(),
    );
    this.accounts$ = this.paymentDataService.getPayeesAndAccounts().pipe(
      map(data => {
        this.accounts = data.accounts;
        this.patchFormValue({ accountFrom: data.accounts?.[0] });
        return data.accounts;
      }),
      shareReplay(),
    );

    this.payeeDataService.selectedDeletePayee
      .pipe(takeUntil(this.destroy$), skip(1))
      .subscribe(payee => (payee ? (this.isDeleteModalOpened = true) : this.openNavigateModal()));

    this.payeeDataService.selectedDeleteEbills
      .pipe(takeUntil(this.destroy$), skip(1))
      .subscribe(payee => (payee ? (this.isDeleteEbillsModalOpen = true) : this.openNavigateModal()));
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  handleClearForm() {
    this.patchFormValue();
    this.paymentsFormGroup.markAsUntouched();
    this.paymentStateService.isFormProcessing = false;
  }

  defaultFormState(payeeSummary: PayeeSummary) {
    const address = this.hasOvernightCheck(payeeSummary?.paymentServices)
      ? payeeSummary?.overnightDeliveryAddress
      : ({} as Address);
    const paymentServiceDate = this.scheduleService.estimatedPaymentServiceDate(
      payeeSummary?.paymentServices,
      PaymentServicesTypes.Regular,
    );

    const minDate = this.scheduleService.estimatedPaymentServiceDate(
      payeeSummary?.paymentServices,
      PaymentServicesTypes.Regular,
    );

    return {
      accountFrom: this.accounts?.[0],
      payeeTo: payeeSummary,
      amount: {
        amount: ' ',
        currency: this.configService.paymentDefaultCurrency,
      },
      memo: '',
      overnightDeliveryAddress: {
        address1: address?.address1,
        address2: address?.address2,
        city: address?.city,
        state: address?.state,
        postalCode: address?.postalCode,
      },
      schedule: {
        frequency: FrequencyKeys.Once,
        startDate: minDate,
        sendDate: minDate,
        repeat: 1,
        endDate: minDate,
        endType: EndTypes.Never,
        paymentService: this.defaultPaymentService,
        paymentServiceDate,
        alertPendingCheckbox: false,
        alertSentCheckbox: false,
        alertLastPendingCheckbox: false,
      },
    };
  }

  areBothListsPresent({ ebillsEnabled, ebillsNotEnabled }: PayeeSummariesByEbill): boolean {
    return Boolean(ebillsEnabled?.length && ebillsNotEnabled?.length);
  }

  createForm(payeeSummaries: PayeeSummary[]) {
    payeeSummaries.forEach(payee => {
      const defaults = this.defaultFormState(payee);
      this.paymentsFormGroup?.registerControl(
        payee.id,
        this.fb.group({
          accountFrom: [defaults.accountFrom],
          payeeTo: [defaults.payeeTo],
          amount: this.fb.group({
            amount: [defaults.amount.amount],
            currency: [defaults.amount.currency],
          }),
          memo: [defaults.memo],
          overnightDeliveryAddress: this.createOvernightAddressFormGroup(payee),
          schedule: this.fb.group({
            frequency: defaults.schedule.frequency,
            startDate: [defaults.schedule.startDate],
            sendDate: [defaults.schedule.sendDate],
            repeat: [defaults.schedule.repeat],
            endDate: [defaults.schedule.endDate],
            endType: [defaults.schedule.endType],
            paymentService: [defaults.schedule.paymentService],
            paymentServiceDate: [defaults.schedule.paymentServiceDate],
            alertPendingCheckbox: [defaults.schedule.alertPendingCheckbox],
            alertSentCheckbox: [defaults.schedule.alertSentCheckbox],
            alertLastPendingCheckbox: [defaults.schedule.alertLastPendingCheckbox],
          }),
        }),
      );
    });
  }

  onModalResolve(isConfirmed: boolean) {
    this.discardModalService.onModalResolve(isConfirmed);
  }

  private createOvernightAddressFormGroup(payeeSummary: PayeeSummary): FormGroup {
    const address = this.hasOvernightCheck(payeeSummary.paymentServices)
      ? payeeSummary.overnightDeliveryAddress
      : ({} as Address);
    const paymentService = this.defaultPaymentService(payeeSummary);
    const isOvernightAddressDisabled =
      !paymentService?.paymentServiceType || paymentService.paymentServiceType !== PaymentServicesTypes.Overnight;

    return this.fb.group({
      address1: [
        {
          value: address?.address1,
          disabled: isOvernightAddressDisabled,
        },
      ],
      address2: [
        {
          value: address?.address2,
          disabled: isOvernightAddressDisabled,
        },
      ],
      city: [
        {
          value: address?.city,
          disabled: isOvernightAddressDisabled,
        },
      ],
      state: [
        {
          value: address?.state,
          disabled: isOvernightAddressDisabled,
        },
      ],
      postalCode: [
        {
          value: address?.postalCode,
          disabled: isOvernightAddressDisabled,
        },
      ],
    });
  }

  private hasOvernightCheck = (services: PaymentService[] | undefined): boolean => {
    return !!services?.find(item => item.paymentServiceType === PaymentServicesTypes.Overnight);
  };

  private defaultPaymentService(payee: PayeeSummary): PaymentServiceSummary | undefined {
    return payee?.paymentServices?.find(
      (item: PaymentServiceSummary) => item.paymentServiceType === PaymentServicesTypes.Regular,
    );
  }

  handleSubmit() {
    const paymentForms = Object.entries(this.paymentsFormGroup.value as PaymentFormState[])
      .filter(([_id, form]) => !!+form.amount.amount)
      .map(([_id, form]) => this.processFormData(form));
    this.paymentStateService.isFormProcessing = true;
    this.paymentStateService.updatePaymentState(paymentForms as any);
    this.navigationService.navigateToReview({ relativeTo: this.route.parent });
  }

  private processFormData(data: PaymentFormState): PaymentFormState {
    const startDate = data.schedule.frequency === FrequencyKeys.Once ? data.schedule.sendDate : data.schedule.startDate;
    switch (data.schedule.endType) {
      case EndTypes.After:
        return {
          ...data,
          schedule: {
            ...data.schedule,
            startDate: startDate || '',
            endDate: new Date(
              this.scheduleService.estimatedEndDate(
                data.schedule.startDate,
                data.schedule.frequency,
                data.schedule.repeat,
              ),
            ).toISOString(),
          },
        };
      case EndTypes.On:
        return {
          ...data,
          schedule: {
            ...data.schedule,
            startDate: startDate || '',
            repeat: this.scheduleService.estimatedOccurences(
              data.schedule.startDate,
              data.schedule.endDate || data.schedule.startDate,
              data.schedule.frequency,
            ),
          },
        };
      default:
        return {
          ...data,
          schedule: {
            ...data.schedule,
            startDate: startDate || '',
          },
        };
    }
  }

  private patchFormValue(value?: any) {
    this.payeeSummaries?.forEach(payee => {
      const formValue = value || this.defaultFormState(payee);
      this.paymentsFormGroup.controls[payee.id].patchValue(formValue);
    });
  }

  onDeleteEbills(resolve: boolean) {
    this.isDeleteEbillsModalOpen = false;
    if (resolve) {
      this.deleteEbills()
        .pipe(take(1))
        .subscribe(() => {
          this.deletionNotify('ebills');
        });
    }
  }

  onDeletePayee(resolve: boolean) {
    this.isDeleteModalOpened = false;
    if (resolve) {
      this.deletePayee()
        .pipe(take(1))
        .subscribe(() => {
          this.deletionNotify('delete');
        });
    }
  }

  onNavigateToPayments(hasNavigated: boolean) {
    if (hasNavigated) {
      this.navigationService.navigateToPayments();
    }
    this.isNavigateModalOpened = false;
  }

  private deletionNotify(type: string) {
    this.payeeDataService.reloadPayees();
    this.notificationType = type;
    this.notificationService.showNotification({
      header: this.notificationHeaderTemplate,
      message: this.notificationMessageTemplate,
      modifier: 'success',
      ttl: this.configService.notificationDismissTime,
    });
  }

  private openNavigateModal() {
    this.isNavigateModalOpened = true;
  }

  openModal(): Observable<boolean> {
    this.cdRef.markForCheck();
    return this.paymentsFormGroup.touched || this.paymentStateService.isFormProcessing
      ? this.discardModalService.openModal()
      : of(true);
  }

  private patchPaymentDataToForm() {
    this.paymentStateService.paymentState.pipe(take(1)).subscribe(paymentsState => {
      if (paymentsState) {
        paymentsState.forEach(payment => {
          this.paymentsFormGroup.get(payment.payeeTo.id)?.patchValue(payment);
        });
      }
    });
  }

  private deletePayee(): Observable<HttpResponse<any> | {}> {
    return this.payeeDataService.selectedDeletePayee.pipe(
      switchMap((payee: any) =>
        payee ? this.payeeDataService.deletePayee(payee.id, payee.electronic) : (of() as Observable<any>),
      ),
    );
  }
  private deleteEbills(): Observable<HttpResponse<PayeeSummary> | {}> {
    return this.payeeDataService.selectedDeleteEbills.pipe(
      switchMap((payee: any) => (payee ? this.payeeDataService.deleteEbills(payee.id) : (of() as Observable<any>))),
    );
  }
}
