import { Component, HostListener, Input, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { combineLatest, Observable, of } from 'rxjs';

import { BillPayAccountsGetResponseBody, BillPayPayeesSummaryGetResponseBody } from '@backbase/data-ang/billpay';
import {
  BillpayDiscardChangesFormComponent,
  BillpayDiscardChangesModalService,
  BillpayPaymentDataService,
  Frequency,
  LoadingState,
  PaymentFormState,
} from '@backbase/billpay-journeys-common';
import { PayBillsStateService } from '../../../services/pay-bills-state.service';
import { PaymentRouteParams } from '../../../model/route.model';
import { PayBillsNavigationService } from '../../../services/pay-bills-navigation.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'bb-billpay-payment-form-container',
  templateUrl: './billpay-payment-form-container.component.html',
  providers: [BillpayDiscardChangesModalService],
})
export class BillpayPaymentFormContainerComponent implements OnInit, BillpayDiscardChangesFormComponent {
  paymentRouteObservable$: Observable<PaymentRouteParams>;
  paymentDataObservable$:
    | Observable<(BillPayPayeesSummaryGetResponseBody & BillPayAccountsGetResponseBody) | undefined>
    | undefined;

  private isFormTouched = false;
  @Input() frequencies?: Frequency[];

  /** @internal */
  constructor(
    private readonly routerService: PayBillsNavigationService,
    private readonly paymentDataService: BillpayPaymentDataService,
    private readonly paymentStateService: PayBillsStateService,
    private readonly discardModalService: BillpayDiscardChangesModalService,
    private readonly route: ActivatedRoute,
  ) {
    this.paymentRouteObservable$ = this.routerService.routeParams;
  }

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  @Input() accountNumberMasked?: boolean;
  @Input() paymentDetailsTitle?: string;
  @Input() paymentDefaultCurrency?: string;

  ngOnInit() {
    this.paymentDataObservable$ = combineLatest([
      this.paymentRouteObservable$,
      this.paymentDataService.getPayeesAndAccounts(),
    ]).pipe(map(([routeParams, payeeSummariesAndAccounts]) => ({ ...routeParams, ...payeeSummariesAndAccounts })));
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if ((this.isFormTouched || this.paymentStateService.isFormProcessing) && !this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  hasNoErrorState(data: BillPayPayeesSummaryGetResponseBody & BillPayAccountsGetResponseBody) {
    return (
      this.payeesLoadingState !== LoadingState.Error &&
      this.accountsLoadingState !== LoadingState.Error &&
      !!data.accounts &&
      !!data.payeeSummaries
    );
  }

  onSubmittedFormData(data: PaymentFormState) {
    this.paymentStateService.isFormProcessing = true;
    this.paymentStateService.updatePaymentState([data]);
    this.routerService.navigateToReview({ relativeTo: this.route.parent });
  }

  get paymentState() {
    return this.paymentStateService.paymentState.pipe(map((state: PaymentFormState[] | undefined) => state?.[0]));
  }

  get payeesLoadingState() {
    return this.paymentDataService.payeesLoadingState;
  }

  get accountsLoadingState() {
    return this.paymentDataService.accountsLoadingState;
  }

  openModal(): Observable<boolean> {
    return this.isFormTouched || this.paymentStateService.isFormProcessing
      ? this.discardModalService.openModal()
      : of(true);
  }

  onModalResolve(isConfirmed: boolean) {
    this.discardModalService.onModalResolve(isConfirmed);
  }

  onFormValueChanged() {
    this.isFormTouched = true;
  }

  onCancelForm() {
    this.routerService.navigateToPayeeList();
  }
}
