import {
  Component,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  ViewChild,
  ElementRef,
  HostListener,
  OnDestroy,
} from '@angular/core';
import {
  BillpayDiscardChangesModalService,
  BillpayPaymentDataService,
  PaymentFormState,
  BillpayUtilityService as Utility,
} from '@backbase/billpay-journeys-common';
import { PayBillsNavigationService } from '../../../services/pay-bills-navigation.service';
import { forkJoin, Observable, of, Subject } from 'rxjs';
import { catchError, map, mapTo, takeUntil, tap } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { PayBillsStateService } from '../../../services/pay-bills-state.service';
import { PaymentResult } from '../../../model/payment.model';

@Component({
  selector: 'bb-billpay-payment-multiple-bills-review',
  templateUrl: 'billpay-payment-multiple-bills-review.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [BillpayDiscardChangesModalService],
})
export class BillpayPaymentMultipleBillsReviewComponent implements OnDestroy {
  private readonly destroy$ = new Subject();
  private editMode = false;
  /**
   * Flag for payment error
   */
  paymentError = false;
  /**
   * Flag for submitting payment
   */
  submittingPayment = false;
  readonly id = Utility.generateId();
  /**
   * Refrence for fail alert
   */
  @ViewChild('failAlert') failAlert!: ElementRef;
  /**
   * List of payments
   */
  payments$ = this.paymentStateService.paymentState.pipe(
    tap(paymentForms => {
      if (!paymentForms?.length) {
        this.navigationService.navigateToForm({ relativeTo: this.route.parent });
      }
    }),
  );
  /**
   * Carry the amount for total amount to be paid
   */
  totalAmount$ = this.payments$.pipe(
    map(payments => payments?.reduce((acc: number, payment: PaymentFormState) => acc + +payment.amount.amount, 0)),
  );
  /**
   * List of responses results
   */
  results?: PaymentResult[] = [];

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  constructor(
    private readonly paymentStateService: PayBillsStateService,
    private readonly dataService: BillpayPaymentDataService,
    private readonly navigationService: PayBillsNavigationService,
    private readonly route: ActivatedRoute,
    private readonly discardModalService: BillpayDiscardChangesModalService,
    private cdRef: ChangeDetectorRef,
  ) {}

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if (!this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  /**
   * Methos to send payment to server
   */
  onPaymentConfirm(payments: PaymentFormState[]) {
    this.paymentError = false;
    this.results = [];
    this.submittingPayment = true;
    forkJoin(
      payments.map(payment =>
        this.dataService.createPayment(payment).pipe(
          mapTo({ ...payment, succeeded: true }),
          catchError(() => of({ succeeded: false, ...payment })),
        ),
      ),
    )
      .pipe(takeUntil(this.destroy$))
      .subscribe(async responseList => {
        this.submittingPayment = false;
        this.paymentStateService.isFormProcessing = false;
        const errors = responseList.filter(response => !response?.succeeded);
        errors.length === payments.length ? this.allFailed() : (this.results = responseList);
        this.cdRef.markForCheck();
      });
  }

  private navigateToForm(resetFormValue: boolean = false) {
    if (resetFormValue) {
      this.paymentStateService.reset();
    }
    this.navigationService.navigateToForm({ relativeTo: this.route.parent });
  }

  private allFailed() {
    this.paymentStateService.isFormProcessing = true;
    this.submittingPayment = false;
    this.paymentError = true;
    this.cdRef.markForCheck();
    this.failAlert?.nativeElement.scrollIntoView(false);
  }

  openModal(): Observable<boolean> {
    if (this.paymentStateService.isFormProcessing && !this.editMode && !this.paymentError) {
      return this.discardModalService.openModal();
    }
    return of(true);
  }

  onCancel() {
    this.navigateToForm(!!this.paymentError);
  }

  onModalResolve(isConfirmed: boolean) {
    this.discardModalService.onModalResolve(isConfirmed);
    if (isConfirmed) {
      this.paymentStateService.reset();
    }
  }

  onEdit() {
    this.editMode = true;
    this.navigateToForm();
  }
}
