import { HostListener, OnDestroy, Directive, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable, of, Subject } from 'rxjs';
import { map, shareReplay, take, takeUntil } from 'rxjs/operators';
import { BillPayAccountsGetResponseBody, BillPayPayeesSummaryGetResponseBody } from '@backbase/data-ang/billpay';

import { PayBillsStateService } from '../services/pay-bills-state.service';
import { PayBillsNavigationService } from '../services/pay-bills-navigation.service';
import {
  BillpayDiscardChangesFormComponent,
  BillpayDiscardChangesModalService,
  BillpayPaymentDataService,
  PaymentFormState,
} from '@backbase/billpay-journeys-common';

@Directive()
export abstract class BasePaymentEditFormContainerDirective implements OnDestroy, BillpayDiscardChangesFormComponent {
  readonly id$: Observable<string>;
  readonly payeesAndAccounts$: Observable<BillPayPayeesSummaryGetResponseBody & BillPayAccountsGetResponseBody>;

  protected readonly destroy$ = new Subject<void>();
  private isFormTouched = false;

  @Input() accountNumberMasked?: boolean;
  @Input() paymentDetailsTitle?: string;

  abstract onPaymentSubmitted(payment: PaymentFormState, id: string): void;

  @HostListener('window:beforeunload', ['$event'])
  onWindowUnload($event: BeforeUnloadEvent) {
    if ((this.isFormTouched || this.stateService.isFormProcessing) && !this.isDiscardModalOpen) {
      $event.returnValue = true;
    }
  }

  /** @internal */
  protected constructor(
    protected readonly dataService: BillpayPaymentDataService,
    protected readonly stateService: PayBillsStateService,
    private readonly route: ActivatedRoute,
    private readonly discardModalService: BillpayDiscardChangesModalService,
    readonly routerService: PayBillsNavigationService,
  ) {
    this.id$ = this.route.paramMap.pipe(
      take(1),
      map(params => params.get('id') || ''),
    );

    this.payeesAndAccounts$ = this.dataService.getPayeesAndAccounts().pipe(shareReplay(1), takeUntil(this.destroy$));
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  get isDiscardModalOpen() {
    return this.discardModalService.isDiscardModalOpen;
  }

  openModal(): Observable<boolean> {
    return this.isFormTouched || this.stateService.isFormProcessing ? this.discardModalService.openModal() : of(true);
  }

  onModalResolve(isConfirmed: boolean) {
    this.discardModalService.onModalResolve(isConfirmed);
  }

  onFormValueChanged() {
    this.isFormTouched = true;
  }

  onCancelForm() {
    this.routerService.navigateToPayeeList();
  }
}
