import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  ValidatorFn,
  Validators,
} from '@angular/forms';
import { BehaviorSubject, Subject } from 'rxjs';

import { Account, Address, EbillSummary, PayeeSummary, PaymentService } from '@backbase/data-ang/billpay';

import {
  AccountNumberFormat,
  accountNumberFullFormat,
  accountNumberMaskedFormat,
  BillpayPaymentScheduleService,
  EndTypes,
  formControlOptions,
  frequencies,
  PaymentType,
  PaymentAmount,
  RECURRING_PAYMENT,
  ONE_OFF_PAYMENT,
  Frequency,
  FrequencyKeys,
  PaymentFormState,
  PaymentServicesTypes,
  Schedule,
  PaymentServiceRadio,
  BillpayUtilityService as Utility,
} from '@backbase/billpay-journeys-common';
import { take, takeUntil } from 'rxjs/operators';
import { ProductSelectorComponent } from '@backbase/ui-ang/product-selector';

@Component({
  selector: 'bb-billpay-payment-form',
  templateUrl: './billpay-payment-form.component.html',
})
export class BillpayPaymentFormComponent implements OnInit, OnDestroy, AfterViewInit {
  /**
   * Array of payee summary details.
   */
  @Input() payeeSummaries: PayeeSummary[] | undefined;
  /**
   * Array of user's accounts.
   */
  @Input() accounts: Account[] | undefined;
  /**
   * Identifier for the payee that is currently selected to be paid from.
   */
  @Input() selectedPayeeId = '';
  /**
   * Details of the payment form.
   */
  @Input() paymentState: PaymentFormState | undefined;
  /**
   * Identifier for the payment when the form is an edit payment.
   */
  @Input() paymentId = '';
  /**
   * Whether the account number should be masked. Defaults to true.
   */
  @Input() accountNumberMask = true;

  /**
   * The type of the payment.
   */
  @Input() paymentType?: PaymentType;
  /**
   * Activates edit payment elements. Defaults to false.
   */
  @Input() isEditPayment = false;
  /**
   * get the payment details title preference. default to 'Payment details'.
   */
  @Input() paymentDetailsTitle = 'Payment details';

  /**
   * get the payment default currency preference. default to 'USD'.
   */
  @Input() paymentDefaultCurrency = 'USD';
  /**
   * Frequencies for schedule
   */
  private _frequencies?: Frequency[];
  @Input() set frequencies(value: Frequency[] | undefined) {
    this._frequencies = value;
  }
  get frequencies(): Array<Frequency> | undefined {
    const isEditRecurring = this.isEditPayment && this.paymentType === RECURRING_PAYMENT;
    const usableFrequencies = this._frequencies ? this._frequencies : frequencies;
    return isEditRecurring ? usableFrequencies.filter(freq => freq.value !== FrequencyKeys.Once) : usableFrequencies;
  }

  @ViewChild('fromAccount') fromAccount!: ProductSelectorComponent;

  @Output() submitPayment = new EventEmitter();
  @Output() cancelPayment = new EventEmitter();
  @Output() valueChanged = new EventEmitter<void>();

  /**
   * Payment form group
   */
  paymentForm: FormGroup = this.fb.group({});
  /**
   * Flag to display if payment is submitted
   */
  formSubmitted = false;
  ebillSummary: EbillSummary | undefined;
  defaultAmount!: PaymentAmount;
  readonly minDate$ = new BehaviorSubject<string>(this.scheduleService.today);
  readonly selectedAccount$ = new BehaviorSubject<Account | undefined>(undefined);
  readonly selectedPayee$ = new BehaviorSubject<PayeeSummary | undefined>(undefined);
  private readonly destroy$ = new Subject<void>();
  /**
   * Maximum occurrences for the number of occurrences input box.
   */
  readonly maxOccurs = 999;
  /**
   * Minimum occurrences for the number of occurrences input box.
   */
  readonly minOccurs = 1;
  /**
   * unique key used for ids
   */
  readonly id = Utility.generateId();

  /** @internal */
  constructor(private readonly fb: FormBuilder, private readonly scheduleService: BillpayPaymentScheduleService) {}

  ngOnInit() {
    this.defaultAmount = {
      amount: '',
      currency: this.paymentDefaultCurrency,
      type: undefined,
    };
    this.selectedAccount$.next(this.defaultAccountFrom);
    this.selectedPayee$.next(this.defaultPayeeTo);
    if (this.defaultPayeeTo) {
      this.ebillSummary = this.defaultPayeeTo.ebill;
    }
    this.minDate$.next(
      this.scheduleService.estimatedPaymentServiceDate(
        this.defaultPayeeTo && this.defaultPayeeTo.paymentServices,
        PaymentServicesTypes.Regular,
      ),
    );
    this.createForm();
    this.proccessPaymentServiceValueChanges();
    this.proccessFrequencyValueChanges();

    this.paymentForm.valueChanges.pipe(takeUntil(this.destroy$), take(1)).subscribe(() => {
      this.valueChanged.emit();
    });
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.fromAccount) {
        this.fromAccount.dropdownToggle.nativeElement.focus();
      }
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  cancel() {
    this.cancelPayment.emit();
  }

  get defaultPaymentServiceType(): PaymentServicesTypes {
    const payee = this.selectedPayee$.getValue();
    return (
      ((this.paymentState &&
        payee &&
        this.paymentState.payeeTo.id === payee.id &&
        this.paymentState.schedule &&
        this.paymentState.schedule.paymentService &&
        this.paymentState.schedule.paymentService.index) as PaymentServicesTypes) || PaymentServicesTypes.Regular
    );
  }

  get accountNumberFormatConfig(): AccountNumberFormat {
    return this.accountNumberMask ? accountNumberMaskedFormat : accountNumberFullFormat;
  }

  get ebillPayment(): boolean {
    return !!(
      !this.isEditPayment &&
      this.ebillSummary &&
      this.ebillSummary.latestBill &&
      this.ebillSummary.latestBill.status === 'UNPAID' &&
      (!this.ebillSummary.status || this.ebillSummary.status === 'ACTIVE')
    );
  }

  get frequencyKeys() {
    return FrequencyKeys;
  }

  get schedule() {
    return this.paymentForm?.controls['schedule'].value;
  }

  get amountGroup() {
    return this.paymentForm?.get('amount');
  }

  get startDateControl() {
    return this.paymentForm.get('schedule.startDate');
  }

  get endDateControl() {
    return this.paymentForm.get('schedule.endDate');
  }

  get scheduleLabelData(): Schedule {
    const schedule = this.schedule;
    return {
      ...schedule,
      endDate: schedule.endType === EndTypes.On ? schedule.endDate : undefined,
      repeat: schedule.endType === EndTypes.After ? schedule.repeat : undefined,
    };
  }

  private get defaultAccountFrom(): Account | undefined {
    return this.paymentState && this.paymentState.accountFrom ? this.paymentState.accountFrom : this.accounts?.[0];
  }

  private get defaultPayeeTo(): PayeeSummary | undefined {
    return this.paymentState?.payeeTo
      ? this.paymentState.payeeTo
      : this.payeeSummaries && this.payeeSummaries.find(acc => acc.id === this.selectedPayeeId);
  }

  private getOvernightAddress(payee: PayeeSummary): Address | undefined {
    return (this.hasOvernightCheck(payee.paymentServices) && payee.overnightDeliveryAddress) || undefined;
  }

  private hasOvernightCheck(services: PaymentService[] | undefined): boolean {
    return !!services?.find((item: PaymentService) => item.paymentServiceType === PaymentServicesTypes.Overnight);
  }

  private get defaultOvernightAddressState(): Address {
    const payee = this.selectedPayee$.getValue() || ({} as PayeeSummary);
    const address = this.getOvernightAddress(payee) || ({} as Address);
    return {
      address1: address.address1 || '',
      address2: address.address2 || '',
      city: address.city || '',
      state: address.state,
      postalCode: address.postalCode || '',
    };
  }

  private get defaultFormState(): PaymentFormState {
    const payee = this.selectedPayee$.getValue() || ({} as PayeeSummary);
    const paymentPayeeService =
      payee.paymentServices &&
      payee.paymentServices.find((item: PaymentService) => item.paymentServiceType === this.defaultPaymentServiceType);
    const paymentPayeeServiceDate =
      !this.paymentState || this.paymentState.schedule.frequency === this.frequencyKeys.Once
        ? payee.paymentServices &&
          this.scheduleService.estimatedPaymentServiceDate(payee.paymentServices, this.defaultPaymentServiceType)
        : undefined;

    const {
      paymentId,
      accountFrom = this.selectedAccount$.getValue() || ({} as Account),
      payeeTo = payee,
      amount = this.defaultAmount,
      memo = '',
      overnightDeliveryAddress = this.defaultOvernightAddressState,
      schedule: {
        frequency = this.findDefaultFrequency(),
        startDate = this.minDate$.getValue(),
        endDate = this.minDate$.getValue(),
        repeat = 1,
        endType = EndTypes.Never,
        paymentService = this.getDefaultPaymentService(paymentPayeeService),
        paymentServiceDate = paymentPayeeServiceDate,
        alertPendingCheckbox = false,
        alertSentCheckbox = false,
        alertLastPendingCheckbox = false,
      } = {},
    } = this.paymentState || ({} as PaymentFormState);
    return {
      paymentId,
      accountFrom,
      payeeTo,
      amount,
      memo,
      overnightDeliveryAddress,
      schedule: {
        frequency,
        startDate,
        endDate,
        repeat,
        endType,
        paymentService,
        paymentServiceDate,
        alertPendingCheckbox,
        alertSentCheckbox,
        alertLastPendingCheckbox,
      },
    };
  }

  private proccessPaymentServiceValueChanges() {
    const paymentServiceControl = this.paymentForm.get('schedule.paymentService');
    const overnightAddressControl = this.paymentForm.controls['overnightDeliveryAddress'];
    if (paymentServiceControl && overnightAddressControl) {
      paymentServiceControl.valueChanges.pipe(takeUntil(this.destroy$)).subscribe(val => {
        if (val.service.paymentServiceType === PaymentServicesTypes.Overnight) {
          overnightAddressControl.enable(formControlOptions);
        } else {
          overnightAddressControl.disable(formControlOptions);
        }
      });
    }
  }

  private proccessFrequencyValueChanges() {
    const frequencyControl = this.paymentForm.get('schedule.frequency');
    const overnightAddressControl = this.paymentForm.controls['overnightDeliveryAddress'];
    if (frequencyControl && overnightAddressControl) {
      frequencyControl.valueChanges.pipe(takeUntil(this.destroy$)).subscribe(val => {
        if (val.value !== FrequencyKeys.Once) {
          overnightAddressControl.disable(formControlOptions);
        }
      });
    }
  }

  private createOvernightAddressFormGroup(): FormGroup {
    const overnightAddress = this.defaultFormState.overnightDeliveryAddress || ({} as Address);
    const paymentService = this.defaultFormState.schedule.paymentService;
    const isOvernightAddressDisabled =
      !paymentService ||
      !paymentService.service ||
      !paymentService.service.paymentServiceType ||
      paymentService.service.paymentServiceType !== PaymentServicesTypes.Overnight;
    return this.fb.group({
      address1: [
        {
          value: overnightAddress.address1,
          disabled: isOvernightAddressDisabled,
        },
      ],
      address2: [
        {
          value: overnightAddress.address2,
          disabled: isOvernightAddressDisabled,
        },
      ],
      city: [
        {
          value: overnightAddress.city,
          disabled: isOvernightAddressDisabled,
        },
      ],
      state: [
        {
          value: overnightAddress.state,
          disabled: isOvernightAddressDisabled,
        },
      ],
      postalCode: [
        {
          value: overnightAddress.postalCode,
          disabled: isOvernightAddressDisabled,
        },
      ],
    });
  }

  private createScheduleFormGroup(): FormGroup {
    const initState = this.defaultFormState;
    const paymentService = initState.schedule.paymentService;
    const isEndDateDisabled = initState.schedule.endType !== EndTypes.On;
    const isRepeatDisabled = initState.schedule.endType !== EndTypes.After;
    const isStartDateDisabled = !!(
      paymentService &&
      paymentService.index !== PaymentServicesTypes.Other &&
      initState.schedule.frequency === this.frequencyKeys.Once
    );

    //@TODO Replace startDate, endDate validators after fix angular issues
    //https://github.com/angular/angular/issues/13461#issuecomment-502674629
    //https://github.com/angular/angular/issues/10567
    return this.fb.group({
      frequency: [initState.schedule.frequency],
      startDate: [
        {
          value: this.scheduleService.getLocalISODate(initState.schedule.startDate),
          disabled: isStartDateDisabled,
        },
        [Validators.required, this.minDateValidator(this.minDate$.getValue())],
      ],
      endDate: [
        {
          value: this.scheduleService.getLocalISODate(initState.schedule.endDate as string),
          disabled: isEndDateDisabled,
        },
        [Validators.required, this.minDateValidator(this.minDate$.getValue())],
      ],
      repeat: [{ value: initState.schedule.repeat, disabled: isRepeatDisabled }],
      endType: [initState.schedule.endType],
      paymentService: [initState.schedule.paymentService],
      paymentServiceDate: [
        {
          value: initState.schedule.paymentServiceDate,
          disabled: !isStartDateDisabled,
        },
      ],
      alertPendingCheckbox: [initState.schedule.alertPendingCheckbox],
      alertSentCheckbox: [initState.schedule.alertSentCheckbox],
      alertLastPendingCheckbox: [initState.schedule.alertLastPendingCheckbox],
    });
  }

  createForm(): void {
    const initState = this.defaultFormState;
    const amount = initState.amount.amount?.toString() || undefined;
    this.paymentForm = this.fb.group({
      paymentId: [this.paymentId],
      accountFrom: [initState.accountFrom],
      payeeTo: [initState.payeeTo],
      amount: this.fb.group({
        amount: [amount],
        currency: [initState.amount.currency],
        type: [initState.amount.type],
      }),
      memo: [initState.memo],
      overnightDeliveryAddress: this.createOvernightAddressFormGroup(),
      schedule: this.createScheduleFormGroup(),
    });
    this.setValidators();
  }

  amountValidator(control: AbstractControl): ValidationErrors | null {
    if (!control.value.amount || +control.value.amount <= 0) {
      return {
        amountInvalid: false,
      };
    } else {
      // eslint-disable-next-line no-null/no-null
      return null;
    }
  }

  isInvalidControl(control: FormControl): boolean {
    return this.formSubmitted && control.invalid && control.touched;
  }

  private minDateValidator(minDate: string): ValidatorFn {
    return (fg: AbstractControl): ValidationErrors | null => {
      if (!fg.value) {
        // eslint-disable-next-line no-null/no-null
        return null;
      }
      if (new Date(fg.value).setHours(0, 0, 0, 0) < new Date(minDate).setHours(0, 0, 0, 0)) {
        return {
          pastDate: true,
        };
      } else {
        // eslint-disable-next-line no-null/no-null
        return null;
      }
    };
  }

  private notEmptyObjectValidator(control: AbstractControl): ValidationErrors | null {
    // eslint-disable-next-line no-null/no-null
    return Object.keys(control.value).length ? null : { notEmptyObject: false };
  }

  private processFormData(data: PaymentFormState): PaymentFormState {
    switch (data.schedule.endType) {
      case EndTypes.After:
        return {
          ...data,
          schedule: {
            ...data.schedule,
            endDate: new Date(
              this.scheduleService.estimatedEndDate(
                data.schedule.startDate,
                data.schedule.frequency,
                data.schedule.repeat,
              ),
            ).toISOString(),
          },
        };
      case EndTypes.On:
        return {
          ...data,
          schedule: {
            ...data.schedule,
            repeat: this.scheduleService.estimatedOccurences(
              data.schedule.startDate,
              data.schedule.endDate || data.schedule.startDate,
              data.schedule.frequency,
            ),
          },
        };
      default:
        return {
          ...data,
          schedule: {
            ...data.schedule,
            ...(!data.schedule.startDate &&
              this.defaultFormState.schedule.startDate && { startDate: this.defaultFormState.schedule.startDate }),
          },
        };
    }
  }

  private setValidators() {
    ([
      ['accountFrom', [this.notEmptyObjectValidator]],
      ['payeeTo', [this.notEmptyObjectValidator]],
      ['amount', [Validators.required, this.amountValidator]],
      ['schedule.repeat', [Validators.required, Validators.min(this.minOccurs), Validators.max(this.maxOccurs)]],
      ['overnightDeliveryAddress.address1', [Validators.required, Validators.maxLength(40)]],
      ['overnightDeliveryAddress.address2', [Validators.maxLength(40)]],
      ['overnightDeliveryAddress.city', [Validators.required, Validators.maxLength(32)]],
      ['overnightDeliveryAddress.state', [Validators.required]],
      ['overnightDeliveryAddress.postalCode', [Validators.required, Validators.pattern(/^\d{5}(\d{4})?$/)]],
    ] as [string, ValidatorFn[]][]).forEach(([controlName, validators]) => {
      const control = this.paymentForm.get(controlName);
      if (control) {
        control.setValidators(validators);
        control.updateValueAndValidity();
      }
    });
  }

  handleSubmit(): void {
    if (!this.formSubmitted) {
      this.formSubmitted = true;
    }
    if (this.paymentForm.valid) {
      this.submitPayment.emit(this.processFormData(this.paymentForm.value));
    }
    this.paymentForm.markAllAsTouched();
  }

  selectAccount(account: Account | undefined): void {
    this.selectedAccount$.next(account);
    this.paymentForm.controls['accountFrom'].setValue(account);
  }

  private setOvernightAddress(payee: PayeeSummary | undefined) {
    const control = this.paymentForm.controls['overnightDeliveryAddress'];
    if (payee?.overnightDeliveryAddress && this.hasOvernightCheck(payee.paymentServices)) {
      control.setValue({
        address1: payee.overnightDeliveryAddress.address1,
        address2: payee.overnightDeliveryAddress.address2 || '',
        city: payee.overnightDeliveryAddress.city,
        state: payee.overnightDeliveryAddress.state,
        postalCode: payee.overnightDeliveryAddress.postalCode,
      });
    } else {
      control.reset();
    }
  }

  selectPayee(payee: PayeeSummary | undefined): void {
    this.selectedPayee$.next(payee);
    this.paymentForm.controls['payeeTo'].setValue(payee);
    this.minDate$.next(
      this.scheduleService.estimatedPaymentServiceDate(payee && payee.paymentServices, PaymentServicesTypes.Regular),
    );

    if (this.startDateControl && this.endDateControl) {
      this.startDateControl.setValue(this.minDate$.getValue());
      this.endDateControl.setValue(this.minDate$.getValue());
    }

    this.setOvernightAddress(payee);

    if (payee) {
      this.ebillSummary = payee.ebill;
    }
    if (this.amountGroup) {
      this.amountGroup.patchValue({ amount: ' ', type: undefined });
    }
  }

  private getDefaultPaymentService(paymentService?: PaymentService): PaymentServiceRadio {
    return {
      index:
        this.isEditPayment && this.paymentType === ONE_OFF_PAYMENT
          ? PaymentServicesTypes.Other
          : paymentService?.paymentServiceType || '',
      service: paymentService,
    };
  }

  private findDefaultFrequency(): FrequencyKeys {
    let defaultFrequency: Frequency | undefined;

    switch (this.paymentType) {
      case ONE_OFF_PAYMENT: {
        defaultFrequency = this.frequencies?.find(item => item.value === FrequencyKeys.Once);
        break;
      }
      case RECURRING_PAYMENT: {
        defaultFrequency = this.frequencies?.find(item => item.value === FrequencyKeys.Monthly);
        break;
      }
    }

    if (defaultFrequency === undefined) {
      defaultFrequency = this.frequencies?.[0] || { value: FrequencyKeys.Once, name: '' };
    }

    return defaultFrequency.value as FrequencyKeys;
  }
}
