import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { BillpayNotificationRoutingService } from '@backbase/billpay-journeys-common';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { PayBillsNavigationService } from './services/pay-bills-navigation.service';

@Component({
  selector: 'bb-pay-bills-journey',
  templateUrl: './pay-bills-journey.component.html',
  providers: [PayBillsNavigationService],
})
export class PayBillsJourneyComponent implements OnInit, OnDestroy {
  private readonly destroy$ = new Subject<void>();

  constructor(
    private readonly navigationService: PayBillsNavigationService,
    private readonly route: ActivatedRoute,
    private readonly billpayNotificationRoutingService: BillpayNotificationRoutingService,
  ) {}

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngOnInit(): void {
    this.setupNavigationSubscriptions();
  }

  private setupNavigationSubscriptions() {
    this.billpayNotificationRoutingService.upcommingPaymentsSubject$
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => this.navigationService.navigateToPayments());

    this.route.paramMap.pipe(takeUntil(this.destroy$)).subscribe((param: ParamMap) => {
      if (param.has('editRecurringPayment')) {
        this.navigationService.navigateToEditRecurringForm(param.get('editRecurringPayment') as string);
      } else if (param.has('editOneOffPayment')) {
        this.navigationService.navigateToEditOneOffForm(param.get('editOneOffPayment') as string);
      }
    });

    this.billpayNotificationRoutingService.managePayeesSubject$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.navigationService.navigateToAddPayee();
    });
  }
}
