import { Component, ChangeDetectionStrategy, Input } from '@angular/core';
import {
  BillpayPaymentScheduleService,
  BillpayUtilityService as Utility,
  FrequencyKeys,
} from '@backbase/billpay-journeys-common';
import { PayBillsJourneyConfigService } from '../../../services/pay-bills-journey-config.service';
import { PaymentResult } from '../../../model/payment.model';

@Component({
  // eslint-disable-next-line @angular-eslint/component-selector
  selector: '[bb-billpay-payment-multiple-bills-review-item]',
  templateUrl: 'billpay-payment-multiple-bills-review-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentMultipleBillsReviewItemComponent {
  @Input() payment?: PaymentResult;
  @Input() last?: boolean;
  isCollapsed = true;
  readonly id = Utility.generateId();

  get endDate(): number {
    const { startDate, repeat, frequency } = this.payment?.schedule || {};
    const date = startDate || new Date().toISOString();
    return this.scheduleService.estimatedEndDate(date, frequency as FrequencyKeys, repeat);
  }

  get frequencyLabel(): string {
    const frequency = this.configservice.frequencies.find(
      frequency => this.payment?.schedule.frequency === frequency.value,
    );
    return $localize`:@@pay-bill-journey.review-item.frequency-label:${frequency?.name || ''}`;
  }

  constructor(
    private readonly scheduleService: BillpayPaymentScheduleService,
    private readonly configservice: PayBillsJourneyConfigService,
  ) {}
}
