import { Injectable } from '@angular/core';
import { PaymentFormState } from '@backbase/billpay-journeys-common';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class PayBillsStateService {
  private readonly paymentStateSource = new BehaviorSubject<PaymentFormState[] | undefined>(undefined);
  readonly paymentState: Observable<PaymentFormState[] | undefined> = this.paymentStateSource.asObservable();

  private isFormProcessingValue = false;
  set isFormProcessing(value: boolean) {
    this.isFormProcessingValue = value;
  }
  get isFormProcessing() {
    return this.isFormProcessingValue;
  }

  updatePaymentState(state?: PaymentFormState[]) {
    this.paymentStateSource.next(state);
  }

  reset() {
    this.isFormProcessing = false;
    this.updatePaymentState();
  }
}
