import { Component, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'bb-billpay-payment-discard-changes-modal',
  templateUrl: './billpay-payment-discard-changes-modal.component.html',
})
export class BillpayPaymentDiscardChangesModalComponent {
  /**
   * Modal title.
   */
  @Input() title?: string;
  /**
   * Modal subtitle.
   */
  @Input() subtitle?: string;
  /**
   * Modal confirm button text.
   */
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('confirm-button-label') confirmButtonLabel?: string;
  /**
   * Modal cancel button text.
   */
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('cancel-button-label') cancelButtonLabel?: string;

  @Output() resolve = new EventEmitter<boolean>();

  cancelModal() {
    this.resolve.emit(false);
  }

  confirmModal() {
    this.resolve.emit(true);
  }
}
